.IndividualProduct-page{
    margin-top: 15vh;
    margin-bottom: 10vh;
}

.IndividualProduct-page span{
    display: flex;
    align-items: center;
    font-size: 1.4rem;
    cursor: pointer;
    margin-left: 10px;
}
.IndividualProduct-page span p{
    padding-bottom: 3px;
    margin: 0;
    font-size: 1rem;
}

.IndividualProduct-body{
    display: flex;
    justify-content: center;
    margin-top: 3vh;
}
.carousel-inner{
    max-width: 450px;
    width: 100%;
}
.carousel-inner img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

