.IndividualProduct-data{
    margin-top: 1vh;
    width: 46%;
}

.IndividualProduct-data h3{

    color: #0046FF;
}

.IndividualProduct-data p{
    color: rgb(87, 88, 90);
    
}
.IndividualProduct-data span{
    
    margin: 0;
    display: flex;
    gap: 5px;
}
.IndividualProduct-data span h5{
    font-size: 1.2rem;
    margin: 0;
    color: rgb(44, 87, 99);
}
.IndividualProduct-data span h5 b{
    font-size: 1.2rem;
    font-weight: 600;
}

.IndividualProduct-data span s{
    color: rgb(239, 91, 103);
    font-size: 1rem;
}
.IndividualProduct-data span p{

    color: rgb(7, 92, 152);
}

.IndividualProduct-data h6{
    color: rgb(96, 112, 127);
    margin-top: 3vh;
}

.IndividualProduct-data h6 b{

    font-weight: 600;
}

.IndividualProduct-data span label{

    color: rgb(49, 76, 95);
    font-size: 1.1rem;
    margin-bottom: 3vh;
}

.IndividualProduct-data span select{
    font-size: 1rem;
    color: rgb(7, 92, 152);
    margin-bottom: 2.5vh;
}
.IndividualProduct-data span select option{

    color: rgb(7, 92, 152);
}


.productBuyingButtons{
    margin-top: 10vh;
    display: flex;
    gap: 20px;

}

.productBuyingButtons button{
    width: 200px;
    padding: 10px 0;
    font-weight: 500;
    border: none;
    border-radius: 0.2rem;
}
.productBuyingButtons button:first-child{
    background-color: #504aec;
    color: aliceblue;
}
.productBuyingButtons button:last-child{
    background-color: #eb5a12;
    color: aliceblue;
}


@media screen and (min-width:320px) and (max-width:480px) {
    .IndividualProduct-body{
        flex-direction: column;
        justify-content: center;
    }
    .IndividualProduct-data{
        width: 100%;
        padding: 0px 8px;
    }
}

@media screen and (min-width:768px) {
  .IndividualProduct-data{
        padding: 0px 8px;
    }   
}