.cartPage {
    margin-top: 15vh;
    display: flex;
    justify-content: space-evenly;
}

.cartContents {
    width: 65%;
    background-color: rgba(241, 247, 245, 0.237);
    max-height: 83vh;
    border: 1px solid #d8d8df;
    padding: 4vh 2vw;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    gap: 30px;
}


.cartItem {
    display: flex;
    gap: 3vw;

}

.cartItem img {

    width: 25%;
    height: 25vh;

}

.cartItem-data {}

.cartItem-data h4 {
    font-size: 1.1rem;
    margin: 0;
    font-weight: 600;
    color: #3b5468;
}

.cartItem-data p {
    margin: 0;
    font-size: 0.8rem;
    color: #7b828a;
}

.cartItem-data span {}

.cartItem-data span h5 {
    font-size: 0.9rem;
    margin: 0;
    margin-top: 10px;
    font-weight: 400;
}

.cartItem-data span h5 b {
    font-weight: 600;

}

.cartItem-data button {}

.cartItem-inputs {
    display: flex;
    gap: 40px;
    justify-content: start;
    align-items: center;
}

.cartItem-inputs span {}

.cartItem-inputs span p {
    display: flex;
    gap: 10px;
    margin: 0;
}

.cartItem-inputs span p b {}

.cartItem-inputs div {
    /* width: 20vw; */
    display: flex;
    align-items: center;
    height: 30px;
    gap: 5px;
}

.cartItem-inputs input {
    max-width: 40px;
    padding: 0;
    padding-left: 10px;
    font-size: 0.7rem;
    height: 20px;
}

.cartItem-inputs div button {
    /* height: 20px; */
    padding: 0;
    padding-bottom: 4px;
    margin: 0;
}


.cartPriceBody {
    width: 24%;
    background-color: rgba(240, 245, 245, 0.345);
    /* margin-left: 3%; */
    padding: 3vh 2vw;
    border: 1px solid #dfdadaeb;
    border-radius: 0.3rem;

}

.cartPriceBody h4 {
    margin-bottom: 3vh;
    color: rgb(103, 120, 146);
}

.cartPriceBody span {
    display: flex;
    justify-content: space-between;
    color: rgb(50, 63, 77);
}

.cartPriceBody span b {

    font-weight: 600;
}

.cartPriceBody span p {

    margin-bottom: 10px;
}

.cartPriceBody hr {}

.cartPriceBody h5 {

    font-weight: 400;
    color: rgb(36, 57, 78);
}

.cartPriceBody h5 b {

    font-weight: 600;
}

.cartPriceBody button {

    width: 100%;
    padding: 13px 0;
    font-weight: 600;
    margin: 3vh 0;
    background-color: #504aec;
    color: aliceblue;
    border: none;
    border-radius: 0.2rem;
}


.modal-body .checkout-address section {
    display: flex;
    gap: 3%;
}

.span-child-1 {
    width: 65%;
}


@media screen and (min-width:320px) and (max-width:480px) {
    .cartPage {
        flex-direction: column;
        gap: 15px;
        padding: 0px 12px;
    }
    .cartItem{
        flex-direction: column;
    }
       .cartItem img{
        width: 100%;
        height: fit-content;
       }
    .cartContents {
        width: 100%;
    }
    .cartItem-inputs{
        gap: 15px;
    }
    .cartPriceBody{
        width: 100%;
    }
}

@media screen and (min-width:768px) and (max-width:900px) {
     .cartPage {
        flex-direction: column;
        gap: 15px;
        padding: 0px 12px;
    }
    .cartContents{
        width: 100%;
    }
     .cartPriceBody{
        width: 100%;
    }
}